body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Montserrat', sans-serif;
}
nav {
  height: 50px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  background: rgba(255,255,255,1);
}
nav img {
	height: 25px;
    left: 5%;
    top: 10px;
    position: absolute;
}
nav ul {
	position: absolute;
	right: 5%;
	font-size: 0.8rem;
	top: 7px;
	margin: 0;
}
nav i {
	font-size: 1.1rem;
	cursor: pointer;
}
nav li {
	display: inline-block;
	margin-left: 40px;
}
.nav li a, .nav li i {
	text-decoration: none;
	color: #7B8689;
	-webkit-transition: all 0.2s ease;
	-o-transition: all 0.2s ease;
	transition: all 0.2s ease;
	padding: 10px;
}
.nav a:hover, .nav i:hover {
	color: black;
}
.nav-count {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	position: absolute;
	right:-10px;
	top:-3px;
	background: #92be48;
	border-radius: 100%;
	width: 25px;
	height: 25px;
	padding-top: 5px;
	color: white;
	font-size: 0.7rem;
	-webkit-transition: all 0.2s ease-out;
	-o-transition: all 0.2s ease-out;
	transition: all 0.2s ease-out;
	text-align: center;
	pointer-events: none;
	font-weight: 400;
}
.nav-mobile .nav-count{
	right: 55px;
}
.nav-mobile {
	display: none;
	top:7px;
}
.nav-mobile li {
	color: black;
	padding: 0;
}
.nav-mobile i {
	font-size: 1.2rem;
	padding: 10px 5px;
}
.toggle-nav {
	position: fixed;
	top: 50px;
	background: white;
	width: 100%;
	text-align: center;
	z-index: 100;
	height: 0;
	overflow: hidden;
	-webkit-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
	transition: all 0.3s ease;
	display: none;
	font-family: 'Montserrat', sans-serif;
}
.toggle-nav ul {
	list-style: none;
	padding-left: 0;
}
.toggle-nav li {
	margin: 30px auto;
}
.toggle-nav a {
	text-decoration: none;
	color: black;
}
.cart {
	position: fixed;
	top:0;
	right: 0;
	bottom: 0;
	width: 360px;
	z-index: 9999;
	background:white;
	-webkit-transition: all 0.2s ease;
	-o-transition: all 0.2s ease;
	transition: all 0.2s ease;
	-webkit-box-shadow: 0 2px 10px 0 rgba(40, 40, 40, 0.15);
	        box-shadow: 0 2px 10px 0 rgba(40, 40, 40, 0.15);
}
.cart-header {
	height: 50px;
	border-bottom: 1px solid #eeeeee;
	text-align: center;
	-webkit-box-shadow: 0 4px 5px -3px #ccc;
	        box-shadow: 0 4px 5px -3px #ccc;
}
.cart-title {
	display: inline-block;
	margin-top: 17px;
	font-family: 'Montserrat', sans-serif;
	font-weight: 500;
}
.cart-items {
	overflow-y: auto;
	width: 100%;
	height: 100%;
	/* -webkit-overflow-scrolling: touch; */
	/* -webkit-perspective: 0; */
	z-index: 9999;
}
.cart-blank-space {
	height: 250px;
}
.item-name {
	margin-bottom: 8px;
}
.item-size{
	display: inline-block;
	border: 1px solid #ccc;
	margin-right: 2%;
	padding: 5px 0 5px 10px;
	width: 63%;
	font-size: 0.8rem;
}
.minus, .add, .quantity{
	display: inline-block;
	border: 1px solid #ccc;
	background-color: black;
	color:white;
	text-align: center;
	padding: 4.4px 7px;
}
.minus, .add {
	cursor: pointer;
}
.cart .quantity{
	border-left: none;
	border-right: none;
	background-color: white;
	color: black;
	width: 5%;
}
.cart i {
	cursor: pointer;
}
.fa-trash-alt {
	position: absolute;
	left: 20px;
	top: 16px;
	font-size: 1.3rem;
}
.cart-header .fa-times {
	position: absolute;
	right: 20px;
	top: 14px;
	font-size: 1.6rem;
}
.cart-empty {
	text-align: center;
}
.cart-empty p {
	margin-top: 20%;
}
.cart-count {
	text-align: center;
	background: #f0f0f0;
	height: 31px;
	padding: 4px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.cart-item {
	padding: 10px 20px;
	font-size: 0.9rem;
	border-bottom: 1px solid #ededed;
}
.cart-summary {
	position: absolute;
	width: 100%;
	bottom: 0;
	background: white;
	-webkit-box-shadow: 0 -4px 5px -3px #ccc;
	        box-shadow: 0 -4px 5px -3px #ccc;
}
.cart-total {
	width: 100%;
	padding: 5px 20px;
}
.cart-total th{
	text-align: left;
	font-weight: 400;
}
.cart-total td{
	text-align: right;
}
.discount-grid{
	display: grid;
	grid-template-columns: 1fr 100px;
}
.discount-input {
	outline: none;
	margin-left: 20px;
	margin-bottom: 15px;
	border:1px solid #ccc;
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	padding:5px 10px;
	border-radius: 0%;
	font-size: 0.9rem;
}
.discount-apply {
	background: black;
	color: white;
	border: none;
	cursor: pointer;
	outline: none;
	height: 24px;
	margin: 0 20px;
	margin-top: 4px;
}
.checkout {
	text-align: center;
	background: black;
	color: white;
	padding: 13px;
	cursor: pointer;
}
#title{
	background: #f6f6f6;
	text-align: center;
	padding: 2rem;
	font-family: 'Montserrat', sans-serif;
	margin-top: 40px;
}
#title p{
	font-weight: 400;
	font-size: 0.85rem;
}
@media only screen and (max-width: 730px) {
	.nav{
		display: none;
	}
	.nav-mobile{
		display: block;
	}
	.toggle-nav{
		display: block;
	}
	.cart{
		width: 100%;
		-webkit-transition: all 0s;
		-o-transition: all 0s;
		transition: all 0s;
	}
}
.menu-container {
	height: 31px;
}
#menu {
	background: #f0f0f0;
	width: 100%;
	height: 31px;
	margin: 0 auto;
	text-align: center;
	z-index: 99;
	padding: 6px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	font-family: 'Montserrat', sans-serif;
	font-weight: 500;
}
#menu span{
	padding: 20px;
}
#menu a {
	text-decoration: none;
	color:rgba(0,0,0,0.6);
}
#menu a:hover {
	color: #92be48;
	opacity: 0.6;
	-webkit-transition: all 0.2s ease;
	-o-transition: all 0.2s ease;
	transition: all 0.2s ease;
}
#breakfast,#lunch,#dinner{
	position: relative;
	top: -60px;
	visibility: hidden;
}
.filter {
	text-align: center;
	margin: 30px auto 0 auto;
	/* width: 90%; */
	padding: 10px 0;
	/* border: 1px solid #b5b5b5; */
}
.filter input{
	display: none;
}
.filter label{
	cursor: pointer;
	padding: 8px;
	font-size: 0.8rem;
	position: relative;
	top:-2px;
}
.filter span {
	display: inline-block;
	height: 16px;
	width: 16px;
	border: 1px solid #999;
	border-radius: 15%;
	position: relative;
	cursor: pointer;
	margin-right: 6px;
	top: 4px;
}
.filter input:checked +span {
	background-color: #dde6c9;
	border-color: #dde6c9;
}
.filter input+span::after{
	content: "\f00c";
	font-family: "Font Awesome 5 Free";
	font-weight: 900;
	font-size: 0.6rem;
	position: absolute;
	left: 2px;
	top: 2px;
	color: black;
	-webkit-transform: scale(0);
	    -ms-transform: scale(0);
	        transform: scale(0);
	-webkit-transition: all 0.2s ease;
	-o-transition: all 0.2s ease;
	transition: all 0.2s ease;
}
.filter input:checked +span::after{
	-webkit-transform: scale(0.9);
	    -ms-transform: scale(0.9);
	        transform: scale(0.9);	
}
.products-container h2{
	text-align: center;
	padding: 1rem;
	font-size: 1.5rem;
	margin-top: 0;
}
.grey-background{
	background: #f6f6f6;
}
.products-grid{
	padding-bottom: 30px;
	display: grid;
	grid-template-columns: repeat(auto-fit,minmax(320px,1fr));
	grid-auto-flow: row;
	justify-items: center;
	grid-row-gap: 40px;
	text-align: center;
	/* user-select: none; */
	font-family: 'Montserrat', sans-serif;
	font-size: 0.9rem;
}
.product-container {
	position: relative;
}
.product-container img {
	display: block;
	margin: 0 auto;
	width: 300px;
	cursor: pointer;
}
.product-container p {
	padding: 10px 0;
	margin: 0;
	width: 300px;
	cursor: pointer;
	height: 36px;
}
.product-count {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	position: absolute;
	right: 10px;
	top: 10px;
	background: #92be48;
	border-radius: 100%;
	width: 40px;
	height: 40px;
	padding-top: 9px;
	color: white;
	font-size: 1.1rem;
	pointer-events: none;
	-webkit-transition: all 0.2s ease-out;
	-o-transition: all 0.2s ease-out;
	transition: all 0.2s ease-out;
}
.product-container select {
	width: 210px;
	height: 30px;
	background: white;
	border: 1px solid #b5b5b5;
	outline: none;
	cursor: pointer;
	font-family: 'Montserrat', sans-serif;
	font-size: 0.8rem;
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	padding-left: 10px;
	margin-right: 3px;
}
.product-container select:hover {
	border-color: black;
}
.select {
	display: inline-block;
	position: relative;
}
.select::after {
	content: "\f078";
	font-family: "Font Awesome 5 Free";
	font-weight: 900;
	position: absolute;
	right: 12px;
	top: 22%;
	font-size: 1.1rem;
	pointer-events: none;
}
.product-container i {
	margin-left: 8px;
	font-size: 0.9rem;
	padding: 5px;
	cursor: pointer;
	border: 1px solid black;
	border-radius: 4px;
	width: 16px;
	height: 16px;
	color: white;
	background: black;
}
.nutrition span{
	margin-bottom: 5px;
	padding: 0 12px;
	display: inline-block;
	width: 3rem;
}
.diets span {
	margin-top: 5px;
	color: #92be48;
	padding: 0 8px;
	width: auto;
	font-weight: 500;
}
.nutrition {
	background: rgba(0,0,0,0.6);
	color: white;
	-webkit-transition: all 0.5s ease;
	-o-transition: all 0.5s ease;
	transition: all 0.5s ease;
	pointer-events: none;
	width: 300px;
	position: absolute;
	-webkit-transform: translateY(-100%);
	    -ms-transform: translateY(-100%);
	        transform: translateY(-100%);
	font-size: 0.8rem;
}


